.user-card--signed {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 30px;
  display: flex;
}

.user-card--signed img {
  object-fit: cover;
  border-radius: 100%;
  width: 200px;
  height: 200px;
  box-shadow: 0 19px 38px #0000004d, 0 15px 12px #00000038;
}

.user-card--footer {
  flex-direction: column;
  align-items: center;
  gap: 23px;
  display: flex;
}

#auth-page-header {
  text-align: center;
  font-size: 22px;
  display: flex;
}

body a, body a:hover {
  text-decoration: none;
}

